// Below are utility classes for padding / margin / gutters similar to Bootstrap methodology.
$g-elem-sm: 2rem;
$g-elem: 3rem;
$g-elem-ml: 3.5rem;
$g-elem-l: 4rem;
$g-elem-xl: 6.5rem;

$g-section-sm: 4rem;
$g-section-md: 5.5rem;
$g-section: 6.5rem;
$g-section-ml: 8.5rem;
$g-section-l: 10rem;
$g-section-xl: 15rem;

// Color variables
$text-dark: rgba(#343438, 1);
$primary-dark: #453878;
$secondary-dark: #343438;
$primary-light: #6154c7;
$light: white;

// Utility classes for preset styles -reusable across the website - following the DRY principle for clean codebase
$box-shadow-dark: 0px 3px 6px #00000029;
$transition-fast: all 0.2s;
$transition-med: all 0.5s;

:root {
  --text-light: _varrgba(255, 255, 255, 1);
  --text-dark: #343438;

  //   margins
  --m-sm: 1rem;
  --m-md: 1.5rem;
  --m-lg: 2rem;
  --m-xl: 2.5rem;

  --avado-green: #3de580;
  --jade: #1fab7a;
  --teal: #007373;
  --indigo: #453878;
  --iris: #6154c7;
  --aurora: #9987ff;
  --non-black: #343438;
  --silver: #9b9ba7;
  --zinc: #cacad3;
  --web-bg: #ededf5;
  --pearl: #efefef;
  --steel: #d7d7e0;
  --orange-pop: #ff8f00;
  --dark-teal: #005858;
  --faded-poster: #7fe2d8;
  --robin-blue: #00cdc2;
  --green-sea: #1fac9e;
  --sky: #70d9e5;
  --cyan: #3fc9e6;
  --legacy-blue: #149bd7;
  --light-pink: #ff8ebe;
  --pink: #ff5fa2;
  --magenta: #f04e90;
  --rose: #f7c2d0;
  --salmon: #ff8d94;
  --warm-red: #ff5d54;
  --cream: #ffd4b1;
  --apricot: #ffb762;
  --lemon: #fee5ab;
  --bright-yellow: #fbd872;
  --legacy-yellow: #ffcc33;
  --white: #ffffff;
  --grey: #e4e4e4;
  --light-grey: #f2f2f2;
  --tinted-grey: #ebeaef;
  --lighter-grey: #f9f9fb;
  --dark-grey: #4c4c4c;
  --purple: var(--aurora);
  /**/
  --red: #c50000;
  --greenblue: #1cca9b;
  --bright-orange: #ff5b00;
  --frog-green: #41b804;
  --sunflower-yellow: #ffdf00;
  --primary-blue: #0700ff;
  --black: #000000;
  /* font-family */
  --font-stack: fieldwork, "Century Gothic", CenturyGothic, AppleGothic,
    sans-serif;

  /* Heading sizes */
  --h1-size: 3rem;
  --h2-size: 2.25rem;
  --h3-size: 1.5rem;
  --h4-size: 1.25rem;
  --h5-size: 1.125rem;
  --h6-size: 1rem;
  /* Heading lineHeight */
  --h1-line-height: 3.375rem;
  --h2-line-height: 2.625rem;
  --h3-line-height: 1.75rem;
  --h4-line-height: 1.625rem;
  --h5-line-height: 1.5rem;
  --h6-line-height: 1.125rem;
  /* Links color */
  --default-links: #675aff;
  --visited-links: var(--teal);
  /* body text size */
  --body-xs: 0.875rem;
  --body-s: 1rem;
  --body-r: 1.125rem;
  --body-big: 1.25rem;
  /* body text line height */
  --body-xs-line: 1.625rem;
  --body-s-line: 1.75rem;
  --body-r-line: 1.875rem;
  --body-big-line: 2rem;
  /* styling */
  --round1: 4px;
  --shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
}
